#root {
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
}

.App {
	flex: 1;
	display: flex;
	flex-direction: column;
}

.container {
	margin-top: 35px;
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	align-items: center;
	flex: 1;
}

.card-3d-wrapper {
	border: 2px solid red;
	margin-right: 15px;
}

.arc-slider {
	border: 2px solid red;
}

.nav {
	height: 8rem;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #017aff;
}

.link {
	font-size: 25px;
	padding: 0.7em;
	border-radius: 0.4em;
	color: white;
	border: 1px solid whitesmoke;
	margin-right: 3em;
	transition: all 0.2s;
}
li:last-child > .link {
	margin-right: 0px;
}

.link:hover {
	color: #017aff;
	background-color: whitesmoke;
	border: 1px solid #017aff;
}

.vc {
	display: flex;
	flex-direction: column;
	align-items: center;
}
