.sneaker-card {
	font-family: "Poppins", sans-serif;
	width: 250px;
	min-height: 320px;
	margin: 25px;
	padding: 12px;
	background-color: white;
	border-radius: 25px;
	box-shadow: 0 20px 20px rgba(0, 0, 0, 0.2), 0px 0px 50px rgba(0, 0, 0, 0.2);
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
}

.sneaker-card__circle {
	width: 100px;
	height: 100px;
	background: linear-gradient(to right, rgba(245, 70, 66, 0.75), rgba(8, 83, 156, 0.75));
	position: absolute;
	z-index: -1;
	border-radius: 50%;
}

.sneaker-card__img {
	margin-top: 20px;
	z-index: 2;
	transition: 2s;
}

.sneaker-card__title {
	font-size: 24px;
	font-weight: bold;
}

.sneaker-card__description {
	padding: 0px 10px;
	color: #585858;
	font-size: 12px;
	font-weight: lighter;
}

.sneaker-card__sizes > button {
	padding: 3px 15px;
	box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
	border-radius: 20px;
	cursor: pointer;
	font-weight: bold;
	color: #585858;
	margin-right: 3px;
}

.sneaker-card__purchase {
	padding: 10px 70px;
	background: #f54642;
	border: none;
	color: white;
	cursor: pointer;
	border-radius: 30px;
	font-weight: bolder;
}

.card-hovered img {
	transform: rotateZ(-45deg);
}
